* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

#svgWrapper{
  position: absolute;
  top: 30%;
  left: 20%;
  width: 50%;
  max-width: 500px;
  min-width: 300px;
}
#spot{
  width: 100%;
}

.floatingBubble{
  width: 3em;
  position: absolute;
  bottom: 0;
}
