#loginBtn{
  width: 50%;
  font-size: 40px;
  border: none;
  background: #404040;
  color: #ffffff;
  font-weight: 100;
  padding: .7em;
  text-transform: uppercase;
  border-radius: 6px;
  display: block;
  transition: all 0.3s ease 0s;
}
#loginBtn:hover{
  color: #404040;
  font-weight: 700;
  letter-spacing: 3px;
  background: white;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}

nav{
  background-color: #191414;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100vw;
  position: fixed;
  top: 0;
  z-index: 2;
}
.logo{
  color: #1DB954;
  font-family: 'Inter', sans-serif;
  font-size: 2em;
  font-weight: 800;
}

.list{
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 50%;
}
.list li a{
  text-decoration: none;
  color: white;
}

#canvasContainer{
  width: 100vw;
  height: 100vh;
  margin: 0;
}



.songInfo{
  display: flex;
  align-items: center;
  background: rgba(255,255,255, 0.2);
  backdrop-filter: blur(30px);
  position: relative;
  top: -110px;
  padding: 1em 0;
}

.ListItem{
  max-width: 640px;
  margin: 0 auto;
}

.artistInfo{
  display: flex;
  align-items: center;
  background: rgba(255,255,255, 0.2);
  backdrop-filter: blur(30px);
  position: relative;
  top: -78px;
  padding: 1em 0;
}
